  min-height: 100vh;
  padding-bottom: 10rem;
}
a,h1,h2,h3,h4,h5,p ,input {
  font-family: 'Montserrat', sans-serif;
}
#clients{
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
}
.clients-title{
  display: flex;
  justify-content: center;
}
.clients-logos{
  padding: 1rem;
  display: flex;
  justify-content: center;
  width: auto;
}
.clients-logos div{
  width: 100px;
  margin:0rem 1rem;
}
.container {
  overflow: hidden;
}
.slider {
  animation: slidein 20s linear infinite;
  white-space: nowrap;
  width: calc(100px * var(--amount));
}
.logos {
  width: calc(100px * var(--amount));
  display: inline-block;
  margin: 0px 0;
}
.fab {
  width: 100px;
  animation: fade-in 0.1s cubic-bezier(0.455, 0.01, 0.515, 0.955) forwards;
}
.fab img{
  width: 100%;
}
#customer-wrapper{
  overflow-x: hidden;
}
@keyframes slidein {
  from {
      transform: translate3d(0, 0, 0);
 }
  to {
      transform: translate3d(-100%, 0, 0);
 }
}
@keyframes fade-in {
  0% {
      opacity: 0;
 }
  100% {
      opacity: 1;
 }
}
